.card-three-main-container {
  display: flex;
  width: 100%;
  padding: 0 30px;
  gap: 30px;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-evenly;
  background-color: #e5e5e5;
}

.card-three-main-container-first-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
  background-color: #612d91;
  border-radius: 20px;
  padding: 50px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease-in;
}

.card-three-main-container-first-box:hover {
  cursor: pointer;
  transform: scale(1.05);
}

.card-three-main-container-first-box h1{
  line-height: 1.2;
  margin-bottom: 20px;
  color: #fff;
}

.card-three-main-container-first-box p{
  line-height: 1.5;
  margin-bottom: 20px;
  color: #fff;
}

.card-three-main-container-first-box a{
  align-self: center;
  padding: 10px;
  font-size: large;
  font-weight: bold;
  background-color: #fff;
  color: #612d91;
  width: 50%;
  border: none;
  align-self: center;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}

.card-three-main-container-first-box button{
  padding: 10px;
  font-size: large;
  font-weight: bold;
  background-color: #fff;
  color: #612d91;
  border: none;
}

.card-three-main-container-second-box {
  flex: 1;
}


.card-three-main-container-second-box img{
  max-width: 100%;
  max-width: 100%;
}